@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500);
img {
  max-width: 100%;
  width: 100%;
  height: auto;
}
body,
html {
  font-family: Open Sans, sans-serif;
  color: #7c7778;
}
#pagePreview {
  font-size: 0.5em;
}
