}
#pagePreview .header__title {
  padding: 4.063em;
}
#pagePreview .lead {
  font-size: 2em;
}
#pagePreview .content__left {
  font-size: 1em;
  padding-top: 3em;
  line-height: 1.75;
}
#pagePreview .content__left h3 {
  font-size: 1.170em ;
  letter-spacing: 0.192em;
  line-height: 1.5;
}
#pagePreview .content__left h3 {
