}
.partnerlogo-circle {
  width: 50px;
}
.header__bg {
  background-color: #022735;
}
.header__bg p.lead {
  font-size: 1.25em;
  color: #fff;
}
.header__color {
  padding: 3em 2.5em 2.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
}
.header__color-full {
  -ms-flex-align: center;
  align-items: center;
  padding-bottom: 6em;
}
.header__color-full h1 {
  max-width: 520px;
  text-align: center;
  font-size: 2.25em;
  font-weight: 300;
  line-height: 1.5;
  color: #fff;
  margin-bottom: 15px;
}
.header__color-full h2 {
  max-width: 350px;
  text-align: center;
  font-size: 1.25em;
  line-height: 1.5;
  font-weight: 300;
  color: #fff;
  margin-top: 0;
}
.header__c h1 {
  font-size: 2.25em;
  font-weight: 300;
  line-height: 1.5;
  color: #fff;
  margin-bottom: 25px;
}
.header__c h2 {
  font-size: 1.25em;
  line-height: 1.5;
  font-weight: 300;
  color: #fff;
  margin-top: 0;
}
.main__title {
  color: #fff;
  font-weight: 100;
  font-size: 2.25em;
  margin-bottom: 1.5em;
}
.main__lead {
  font-size: 1.25em;
  color: #fff;
}
.main__form {
  padding: 1.5em 2em;
  background-color: #f0f0f0;
}
.main__form p {
  font-size: 1em;
  font-weight: 700;
  line-height: 1.5;
  color: #000;
}
.header__title {
  background-color: transparent;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 2.5em;
  border-bottom-right-radius: 10px;
}
.header__title h2,
.header__title img {
  margin-bottom: 30px;
}
.header__title h2 {
  color: #fff;
  font-weight: 100;
  font-size: 2.25em;
}
.color--hp {
  color: #405662 !important;
}
.header__assetimg-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.header__assetimg-wrap .assetthumb {
  max-width: 350px;
}
@media (max-width: 767px) {
  .header__assetimg-wrap {
    display: none;
  }
}
.content__copy,
.content__left {
  padding: 1.5em 2.5em;
  line-height: 1.5;
  color: #000;
  font-size: 1em;
}
@media (max-width: 991px) {
  .content__copy,
  .content__left {
    padding: 15px;
  }
}
.content__copy h3,
.content__left h3 {
  color: #000;
  font-size: 1.17em;
  font-weight: 700;
  margin: 0;
  font-family: Open Sans;
}
.content__copy h4,
.content__left h4 {
  font-size: 1em;
  font-weight: 700;
  margin-top: 2.5em;
}
.content__copy ul,
.content__left ul {
  margin-top: 15px;
  margin-bottom: 2.5em;
  padding-left: 2.5rem;
}
.content__copy li,
.content__left li {
  position: relative;
  list-style-type: none;
  margin-bottom: 1.5em;
  font-size: 0.875em;
}
.content__copy ul.regular li,
.content__left ul.regular li {
  list-style-type: disc;
}
.content__copy ul.regular li:before,
.content__left ul.regular li:before {
  content: none;
}
.content__copy ol > li,
.content__left ol > li {
  list-style-type: decimal;
  margin-bottom: .8em;
  margin-left: 1em;
  font-size: 1em;
}
.content__copy ul > li:before,
.content__left ul > li:before {
  content: "";
  position: absolute;
  display: block;
  left: -35px;
  top: 5px;
  width: 8px;
  height: 13px;
  border: solid #00a5d3;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}
.content__copy a,
.content__left a {
  text-decoration: none;
  color: #00a5d3;
}
.logo-partnerex {
  margin-top: 1.5em;
  max-width: 250px;
}
.content__asset {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}
.content__asset img {
  width: 360px;
  height: auto;
}
.content__right {
  border: 1px solid #e9e9e9;
  padding: 1em 1.5em;
  background-color: #eee;
  text-align: center;
}
.content__right h2 {
  color: #000;
  font-weight: 700;
  font-size: 1em;
}
.content__right p.lead {
  margin-top: 1.5em;
  color: #666;
  font-size: 1.25em;
}
.asset--multi {
  max-width: 1200px;
}
.asset--multi .banner {
  padding-top: 35px;
}
.asset--multi .banner img {
  width: 100%;
  height: auto;
}
.asset--multi header {
  background-color: #0483c7;
  padding: 25px 40px 30px;
  color: #fff;
  text-align: center;
  font-weight: 300;
}
.asset--multi header h1 {
  max-width: 700px;
  font-size: 3.75em;
  font-weight: 500;
  line-height: 1.5;
  color: #fff !important;
  margin: .625em 0 0;
}
.asset--multi header h2 {
  max-width: 610px;
  font-size: 1.5em;
  font-weight: 300;
  line-height: 1.5;
  color: #fff !important;
  margin-left: 5px;
  margin-bottom: 1.25em;
}
.asset--multi header h3 {
  font-size: 1.875em;
  line-height: 1.5;
  font-weight: 500;
  color: #fff !important;
  max-width: 600px;
  margin-left: 5px;
}
.asset--multi nav {
  background-color: #eee;
  border-bottom: 3px solid #0483c7;
  margin-top: 5px;
  margin-left: 0;
  margin-right: 0;
  top: 0;
  left: 50%;
  z-index: 99;
  transform: translateX(-50%);
  width: 100% !important;
  max-width: 1200px;
  position: relative;
}
.asset--multi nav .nav--item {
  padding: 1.25em 2.5em;
  text-align: center;
  transition: background-color 0.3s;
  font-size: 18px;
  line-height: 24px;
  color: #000;
}
.asset--multi nav .nav--item:hover {
  cursor: pointer;
}
.asset--multi nav .nav--active,
.asset--multi nav .nav--item:hover {
  color: #fff;
  background-color: #0483c7;
}
.asset--multi-heading h2 {
  font-size: 1.625em;
  font-weight: 300;
  line-height: 36px;
  color: #000;
  margin-right: 20px;
}
.asset--multi-heading span {
  display: inline-block;
  -ms-flex: 1;
  flex: 1;
  height: 1px;
  background-color: #c4c4c4;
}
.asset--multi-row {
  padding: 35px 0;
}
.asset--multi-asset {
  color: #000;
  margin-bottom: 40px;
}
.asset--multi-asset > div {
  border: 1px solid #c4c4c4;
  border-bottom: 5px solid #0483c7;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
}
.asset--multi-asset .padding--body {
  padding: 15px 20px 25px;
}
.asset--multi-asset img {
  width: 100%;
  height: auto;
}
.asset--multi-asset h4 {
  font-size: 1.5em;
  font-weight: 300;
  line-height: 31px;
  min-height: 62px;
  margin-bottom: 5px;
}
.asset--multi-asset p {
  font-size: 0.875em;
  line-height: 20px;
  margin: 15px 0;
  min-height: 60px;
}
.asset--multi-asset a {
  text-transform: uppercase;
  color: #000;
  text-decoration: none;
  font-weight: 700;
  cursor: pointer;
}
.asset--multi-asset a span {
  width: 14px;
  height: 14px;
  position: relative;
  left: 5px;
  top: 1.5px;
  display: inline-block;
  background: url("https://edge.spiceworksstatic.com/service.client-interactive/content/shared/arrow@2x.png")
    no-repeat 50% / cover;
}
.asset--multi .button--nav {
  height: 53px;
  width: 233px;
  border-radius: 5px;
  background-color: #0483c7;
  color: #fff;
  font-family: Open Sans;
  font-size: 0.875em;
  font-weight: 700;
  line-height: 1.2;
  text-align: center;
  display: block;
  margin: 3em auto 2.5em;
}
.asset--multi .button--nav:hover {
  cursor: pointer;
}

.content__right .asset-list li {
